import { Cell, Image, ShareSheet } from "@taroify/core"
import { ArrowRight } from "@taroify/icons"
import { useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicShareSheet() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell
        clickable
        title=""
        rightIcon={<ArrowRight />}
        onClick={() => setOpen(true)}
      />
      <ShareSheet
        open={open}
        onSelect={() => setOpen(false)}
        onClose={setOpen}
        onCancel={() => {
          setOpen(false)
        }}
      >
        <ShareSheet.Backdrop />
        <ShareSheet.Header title="" />
        <ShareSheet.Options>
          <ShareSheet.Option icon="wechat" name="" openType="share" />
          <ShareSheet.Option icon="wechat-moments" name="" />
          <ShareSheet.Option icon="weibo" name="" />
          <ShareSheet.Option icon="qq" name="QQ" />
        </ShareSheet.Options>
        <ShareSheet.Button type="cancel"></ShareSheet.Button>
      </ShareSheet>
    </>
  )
}

function MultilineShareSheet() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell
        clickable
        title=""
        rightIcon={<ArrowRight />}
        onClick={() => setOpen(true)}
      />
      <ShareSheet open={open} onSelect={() => setOpen(false)} onClose={setOpen}>
        <ShareSheet.Header title="" />
        <ShareSheet.Options>
          <ShareSheet.Option icon="wechat" name="" />
          <ShareSheet.Option icon="wechat-moments" name="" />
          <ShareSheet.Option icon="weibo" name="" />
          <ShareSheet.Option icon="qq" name="QQ" />
        </ShareSheet.Options>
        <ShareSheet.Options>
          <ShareSheet.Option icon="link" name="" />
          <ShareSheet.Option icon="poster" name="" />
          <ShareSheet.Option icon="qrcode" name="" />
          <ShareSheet.Option icon="weapp-qrcode" name="" />
        </ShareSheet.Options>
        <ShareSheet.Button type="cancel"></ShareSheet.Button>
      </ShareSheet>
    </>
  )
}

function CustomShareSheet() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell
        clickable
        title=""
        rightIcon={<ArrowRight />}
        onClick={() => setOpen(true)}
      />
      <ShareSheet open={open} onSelect={() => setOpen(false)} onClose={setOpen}>
        <ShareSheet.Options>
          <ShareSheet.Option
            icon={<Image src="https://img.yzcdn.cn/vant/custom-icon-fire.png" />}
            name=""
          />
          <ShareSheet.Option
            icon={<Image src="https://img.yzcdn.cn/vant/custom-icon-light.png" />}
            name=""
          />
          <ShareSheet.Option
            icon={<Image src="https://img.yzcdn.cn/vant/custom-icon-water.png" />}
            name=""
          />
        </ShareSheet.Options>
        <ShareSheet.Button type="cancel"></ShareSheet.Button>
      </ShareSheet>
    </>
  )
}

function ShareSheetWithDescription() {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Cell
        clickable
        title=""
        rightIcon={<ArrowRight />}
        onClick={() => setOpen(true)}
      />
      <ShareSheet open={open} onSelect={() => setOpen(false)} onClose={setOpen}>
        <ShareSheet.Header title=""></ShareSheet.Header>
        <ShareSheet.Options>
          <ShareSheet.Option icon="wechat" name="" />
          <ShareSheet.Option icon="weibo" name="" />
          <ShareSheet.Option icon="link" name="" description="" />
          <ShareSheet.Option icon="poster" name="" />
          <ShareSheet.Option icon="qrcode" name="" />
        </ShareSheet.Options>
        <ShareSheet.Button type="cancel"></ShareSheet.Button>
      </ShareSheet>
    </>
  )
}

export default function ShareSheetDemo() {
  return (
    <Page title="ShareSheet " className="share-sheet-demo">
      <Block variant="card" title="">
        <BasicShareSheet />
      </Block>
      <Block variant="card" title="">
        <MultilineShareSheet />
      </Block>
      <Block variant="card" title="">
        <CustomShareSheet />
      </Block>
      <Block variant="card" title="">
        <ShareSheetWithDescription />
      </Block>
    </Page>
  )
}
